.AppBar {
  padding: 10px 0;
  width: 100%;
  background-color: #fff;
  box-shadow: 0 5px 5px -5px rgba(34, 60, 80, 0.6);
}

.AppBar_Container {
  margin: 0 auto;
  padding: 0 1rem;
  max-width: 1600px;
  box-sizing: border-box;
}

.AppBar_LogoWrapper {
  width: 50px;
  height: 50px;
}

.AppBar_LogoImage {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}